import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/Busquedas_AEI',
    name: 'busquedas',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Busquedas.vue')
  },
  {
    path: '/Presentaciones_PPP',
    name: 'presentaciones',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Presentaciones.vue')
  },
  {
    path: '/Animaciones_APP',
    name: 'animaciones',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/Animaciones.vue')
  },
  {
    path: '/about',
    name: 'About',
    component: () => import('@/components/about.vue')
  },
  {
    path: '/Busquedas_AEI/:id',
    name: 'BAEI',
    component: () => import('@/components/busquedas.vue')
  },
  {
    path: '/Presentaciones_PPP/:id',
    name: 'PPPP',
    component: () => import('@/components/presentaciones.vue')
  },
  {
    path: '/Animaciones_APP/:id',
    name: 'AAPP',
    component: () => import('@/components/animaciones.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
